/* Basic reset */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  /* Body styling */
  body {
    font-family: 'Arial', sans-serif;
    background-color: #f4f4f4;
  }
  
  /* Navigation Menu */
  .navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #333;
    padding: 10px 20px;
  }
  
  .navbar .logo {
    color: #fff;
    font-size: 1.5rem;
  }
  
  .navbar .nav-links {
    list-style: none;
    display: flex;
  }
  
  .navbar .nav-links li {
    margin-left: 20px;
  }
  
  .navbar .nav-links a {
    text-decoration: none;
    color: white;
    font-size: 1rem;
  }
  
  .hamburger {
    display: none;
    cursor: pointer;
  }
  
  .hamburger .line {
    width: 25px;
    height: 3px;
    background-color: white;
    margin: 4px;
  }
  
  /* Hero Section */
  .header {
    background: url('https://assets.codepen.io/3685267/timed-cards-1.jpg') no-repeat center center/cover;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: white;
  }
  
  .heading {
    font-size: 3rem;
    text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.7);
  }
  
  .cta-button {
    padding: 10px 20px;
    background-color: #ecad29;
    color: white;
    border: none;
    border-radius: 5px;
    font-size: 1rem;
    cursor: pointer;
    margin-top: 20px;
  }
  
  .cta-button:hover {
    background-color: #d49320;
  }
  
  /* Mobile View */
  @media (max-width: 768px) {
    .hamburger {
        display: block;
    }
  
    .nav-links {
        display: none;
        flex-direction: column;
    }
  
    .navbar.active .nav-links {
        display: flex;
    }
  };